/* Basic Style */
body {
  background: #222;
  color: #61dafb;
  font-family: Lato, sans-serif;
}
.container {
  box-shadow: 0px 0px 100px #61dafb;
  -moz-box-shadow: 0px 0px 100px #61dafb;
  -webkit-box-shadow: 0px 0px 100px #61dafb;
  border: 5px solid #222;
  border-radius: 10px;
  display: block;
  width: 430px;
  margin: 100px auto 0;
  padding-bottom: 10px;
}
ul {
  margin: 0;
  padding: 0;
}
li * {
  float: left;
}
li,
h3 {
  clear: both;
  list-style: none;
}
input,
button {
  outline: none;
}
button {
  background: none;
  border: 0px;
  color: #888;
  font-size: 15px;
  width: 60px;
  margin: 10px 0 0;
  font-family: Lato, sans-serif;
  cursor: pointer;
}
button:hover {
  color: #333;
}
/* Heading */

h3,
label.new-task {
  font-weight: 700;
  font-size: 15px;
  border-bottom: 2px solid #61dafb;
  padding-bottom: 10px;
  margin: 0;
  text-transform: uppercase;
}
input[type="text"] {
  margin: 0;
  font-size: 18px;
  line-height: 18px;
  height: 18px;
  padding: 10px;
  border: 1px solid #ddd;
  background: #222;
  border-radius: 8px;
  font-family: Lato, sans-serif;
  color: #61dafb;
}

/* New Task */
label.new-task {
  display: block;
  margin: 0 0 20px;
}
input.new-task {
  float: left;
  width: 315px;
}
p > button:hover {
  color: #61dafb;
}

/* Task list */
ul li:first-child {
  border-top: 0;
}
li {
  overflow: hidden;
  padding: 20px 0;
  border-top: 1px solid #777;
}
li div,
input[type:"checkbox"] {
  line-height: 40px;
}
li .checkbox {
  margin: 0 10px;
}
li .label {
  font-size: 18px;
  width: 237px;
  padding: 0 0 0 11px;
}
li .label.done {
  text-decoration: line-through;
  color: #999;
}
li .delete {
  padding-right: 5%;
  font-weight: 400;
  float: right;
}

li .delete:hover {
  color: #cf2323;
}

/* Edit Task */
ul li input[type="text"] {
  display: none;
}

ul li.editMode input[type="text"] {
  display: block;
}

ul li.editMode label {
  display: none;
}
.list-container {
  padding: 0px 10px;
}
.App-logo {
  -webkit-animation: App-logo-spin infinite 4s;
          animation: App-logo-spin infinite 4s;
  height: 80px;
}
.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
}
.App-title {
  color: #61dafb;
  font-size: 1.5em;
}

@-webkit-keyframes App-logo-spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  50% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
  100% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
}

@keyframes App-logo-spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  50% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
  100% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
}



